@import '../../common/constants';
@import '../../common/mixins';
@import '../../common/color.controls';

$calendar-column-width: 8*$grid-size;
$calendar-chevron-width: 10*$grid-size;
$calendar-width: 40*$grid-size;

$line-height-chevron: 4*$grid-size;
$line-height-header: 4*$grid-size;
$line-height-row: $calendar-column-width;

.calendar {
    @include md-box(block, relative);
    font-family: $font-family-default;
    font-size: $font-size-default;
    padding-bottom: 2*$grid-size;
    @include themify {
        color: themed('color-text-rest');
    }
    .calendar-header {
        @include md-box(block, relative);
        margin-left: 2*$grid-size;
        font-family: $font-family-light;
        display: flex;
        align-items: center;
        justify-content: space-between;

        & button {
            text-decoration: none;
            background-color: transparent;
            border: none;

            &:hover {
                cursor: pointer;
            }

            &:focus {
                outline: none;
            }
        }

        .action-bar {
            display: flex;
            align-items: center;
            justify-content: flex-end;
        }

        .calendar-month {
            @include md-box(inline-block, relative);
            margin-top: 4*$grid-size;
            @include themify{
                color: themed('color-text-rest')
            }
            font-size: $font-size-h5;
            line-height: $line-height-header;
            text-align: left;
            vertical-align: top;
            width: 100%
        }

        .calendar-chevron {
            @include md-box(inline-block, relative);
            text-align: center;
            vertical-align: bottom;
            line-height: $line-height-chevron;
            padding-top: 2*$grid-size;
            width: $calendar-chevron-width;
            text-decoration: none;

            &:visited {
                color: inherit;
            }

            &.disabled {
                pointer-events: none;
            }

            &:focus {
                outline-offset: -1px;
            }
        }
    }

    .calendar-days {
        @include md-box(block, relative);
        width: 100%;
        margin: 2*$grid-size 0px;
        padding-left: $grid-size;
        padding-bottom: 1.5*$grid-size;
        @include themify{
            border-bottom: 1px solid themed('color-border-rest');
            color: themed('color-text-rest')
        }
        & > div  {
            @include md-box(inline-block, relative);
            width: $calendar-column-width;
            text-align: center;
        }

        div + div {
            margin-left: $grid-size;
        }
    }

    .calendar-row {
        margin-left: $grid-size;

        & > button.selected {
            @include themify{
                color: themed('color-text-white');
                background-color: themed('color-bg-btn-primary-rest');

                &:focus {
                    outline: 1px dashed themed('color-outline-btn-primary-focus');
                    background-color: themed('color-bg-btn-primary-focus');
                    outline-offset: -2px;
                }
            }
        }

        & > button {
            @include md-box(inline-block, relative);
            width: $calendar-column-width;
            height: $calendar-column-width;

            line-height: $line-height-row;
            text-align: center;
            vertical-align: middle;

            text-decoration: none;
            background-color: transparent;
            border: none;

            &:hover:not(.selected), &:focus:not(.selected)  {
                @include themify{
                    background-color: themed('color-bg-item-hover');
                }
                cursor: pointer;
            }

            &:visited {
                color: inherit;
            }

            &:focus:not(.selected) {
                outline-offset: -2px;
                @include themify{
                    outline: 1px dashed themed('color-border-focus');
                }
            }
        }

        & > button + button {
            margin-left: $grid-size;
        }

        button.disabled {
            @include themify{
                color: themed('color-text-disabled');
            }
        }
    }

    .calendar-row + .calendar-row {
        margin-top: $grid-size;
    }
}